.container {
	width: 800px;
	height: 500px;
	background-color: tomato;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 10%;
	flex-direction: column;
}

input {
	border: none;
	appearance: none;
	background: #f2f2f2;
	padding: 12px;
	border-radius: 3px;
	width: 300px;
	font-size: 14px;
	margin: 20px;
	background-color: limegreen;
}

input::placeholder {
	color: green;
	text-transform: uppercase;
}

img {
	width: 150px;
	height: 100px;
}

p {
	padding: 10px 0;
}

.value {
	text-transform: uppercase;
	font-weight: bold;
	padding: 0 10px;
	color: rgba(31, 13, 94, 0.723);
}
